import React, { Component } from 'react'
import Sup from "./Support.module.css"
import Cliptwo from "../../imgs/Cliptwo.png"
import Clip from "../../imgs/Clip.png"
import Clipbk from "../../imgs/Clipbk.png"
import ClipBo from "../../imgs/Clip_bo.png"
import ClipBs from "../../imgs/Clip_bs.png"
import LAB from "../../imgs/brano.jpg"
import Certif from "../../imgs/Layer.png"
import CertifTwo from "../../imgs/Layer2.png"
import FceBok from "../../imgs/facebook.png"
import Instgrm from "../../imgs/instagram.png"
import Yotub from "../../imgs/youtube.png"

export default class Support extends Component {
  render() {
    return (
      <>
      <div className='text-left' id={Sup.sideBar}>
          <div id={Sup.slidbrOne}>
            <ul id={Sup.sidBrUl}>
                <li id={Sup.sidBrLL}><img src={FceBok} id={Sup.mtImg} alt="" /></li>
            </ul>
          </div>
          <div id={Sup.slidbrTwo}>
            <ul id={Sup.sidBrUl}>
                <li id={Sup.sidBrLL}><img src={Instgrm} id={Sup.mtImgins} alt="" /></li>
            </ul>
          </div>
          <div id={Sup.slidbrThre}>
            <ul id={Sup.sidBrUl}>
                <li id={Sup.sidBrLL}><img src={Yotub} id={Sup.mtImgY} alt="" /></li>
            </ul>
          </div>
      </div>
        <h1 id={Sup.head}>عن معامل المختبر (وحدة الاغذية و المياه)</h1>
        <div id="carouselExampleIndicators" className="carousel slide" data-ride="carousel">
            <ol className="carousel-indicators">
            <li data-target="#carouselExampleIndicators" data-slide-to="0" className="active"></li>
            <li data-target="#carouselExampleIndicators" data-slide-to="1"></li>
            <li data-target="#carouselExampleIndicators" data-slide-to="2"></li>
            </ol>
            <div className="carousel-inner" id={Sup.box}>
            <div className="carousel-item active">
                <p className="d-block w-100" id={Sup.carsoultext}>نحن نعلم أن كل عميل له المتطلبات الخاصة ولدينا المرونة لتقديم أفضل الخدمات المعملية.</p>
            </div>
            <div className="carousel-item">
                <p className="d-block w-100" id={Sup.carsoultext}>معامل المختبر هي معامل تحاليل طبية معتمدة من المجلس الوطني للاعتماد ISO / IEC 17025:2017 والتي قدمت اختبارات ميكروبيولوجية للأغذية والأعلاف.</p>
            </div>
            <div className="carousel-item">
                <p className="d-block w-100" id={Sup.carsoultext}>نحن نقدم اختبارات الغذاء الميكروبيولوجية لاكتشاف مسببات الأمراض التي تنقلها الأغذية والكائنات المسببة للتلف بالإضافة إلى مجموعة واسعة من الخدمات بما في ذلك التحليل الميكروبيولوجي و الكبميائي للمياه.</p>
            </div>
            </div>
            <button id={Sup.sronly} className="carousel-control-prev" type="button" data-target="#carouselExampleIndicators" data-slide="prev">
            <span className="carousel-control-prev-icon" aria-hidden="true"></span>
            <span className="sr-only" >Previous</span>
            </button>
            <button id={Sup.sronly} className="carousel-control-next" type="button" data-target="#carouselExampleIndicators" data-slide="next">
            <span className="carousel-control-next-icon" aria-hidden="true"></span>
            <span className="sr-only">Next</span>
            </button>
        </div>

        <br />

        <div className="container" id={Sup.BoxTwo}>
            <div className='text-center my-5'>
                <h1 id={Sup.txtCntr}>خدماتنا</h1>
                <p id={Sup.textPag}>تحليل الميكروبيولوجي للاغذية</p>
            </div>
        <div className="row row-cols-1 row-cols-md-2">
                <div className="col mb-4">
                    <div className="card" id={Sup.Card}>
                        <img src={Cliptwo} className="card-img-top" alt="..." />
                        <div className="card-body">
                            <p className="card-text" id={Sup.BTXTS}>يعد التلوث الميكروبيولوجي مشكلة خطيرة تتعلق بالسلامة في العديد من الصناعات ، حيث يمكن أن يؤدي إلى مجموعة واسعة من الأمراض التي تسبب مشكلات محتملة تتعلق بالسلامة.</p>
                        </div>
                    </div>
                </div>
                <div className="col mb-4">
                    <div className="card" id={Sup.Card}>
                        <img src={Clip} className="card-img-top" alt="..." />
                        <div className="card-body">
                            <p className="card-text" id={Sup.BTXTS}>نقوم باختبار مجموعة متنوعة من عينات الطعام بما في ذلك اللحوم النيئة والدواجن والمأكولات البحرية والوجبات الجاهزة للأكل والفواكه والخضروات الطازجة والعصائر والحليب ومنتجات الألبان والحبوب والمكسرات والأعشاب والمواد المضافة.</p>
                        </div>
                    </div>
                </div>
        </div>
        </div>
        <br />

        <div className='text-center my-5'>
            <h1 id={Sup.txtCntr}>خدماتنا</h1>
            <p id={Sup.textPag}>تحليل الميكروبيولوجي للاغذية</p>
        </div>
        <div className="container" id={Sup.BoxThree}>
            <div className="row row-cols-1 row-cols-md-3">
                    <div className="col mb-3">
                        <div className="card" id={Sup.CardOffers}>
                            <img src={Clipbk} id={Sup.CdImge} className="card-img-top" alt="..." />
                            <div className="card-body">
                                <p className="card-text text-right">إذا كان الشخص يحتاج إلى 2000 سعر حراري يوميًا ، فيجب أن يستهلك أيضًا 2000 ملليلتر من الماء يوميًا</p>
                            </div>
                        </div>
                    </div>
                    <div className="col mb-3">
                        <div className="card" id={Sup.CardOffers}>
                            <img src={ClipBo} id={Sup.CdImge} className="card-img-top" alt="..." />
                            <div className="card-body">
                                <p className="card-text text-right">توافر جميع أنواع اختبارات المياه بما في ذلك الصناعية و الجوفية والزراعية والصالحة للشرب</p>
                            </div>
                        </div>
                    </div>
                    <div className="col mb-3">
                        <div className="card" id={Sup.CardOffers}>
                            <img src={ClipBs} id={Sup.CdImge} className="card-img-top" alt="..." />
                            <div className="card-body">
                                <p className="card-text text-right">تم اعتماد اختبارات المياه بموجب شهادة EOS 190-01 / 2007</p>
                            </div>
                        </div>
                    </div>
            </div>
        </div>

        <br />
        <br />
        
        <div className="container-fluid">
            <img src={LAB} alt="" id={Sup.BgLap} className='w-100' />
        </div>
        <div className="container text-center my-5">
            <h1 id={Sup.txtH}>شهادات الاعتماد</h1>
        </div>

        <div className='container'>
            <div className="row">
                <div className="col-md-6">
                    <img src={Certif} id={Sup.cnterimgs} className='w-50' alt="" />
                    <h1 id={Sup.headCe} className='text-center'>CAP</h1>
                </div>
                <div className="col-md-6">
                    <img src={CertifTwo} id={Sup.cnterimgs} className='w-50' alt="" />
                    <h1 id={Sup.headCe} className='text-center'>EGAC</h1>
                </div>
            </div>
        </div>
        <div className="container">
            <div className='text-center my-5'>
                <h1 id={Sup.txtH}>المختبر ميجا لاب</h1>
                <p id={Sup.txt}>المعمل المركزي الرئيسي هو قلب عملياتنا وأساس تقنياتنا العلمية. قم بجولة  <br />
                عبر هذا الفيديو لترى لمحة عن كيفية حملنا لمهمتنا إلى القلب.</p>
            </div>
            <div className="row">
                <div className="col-md-12">
                    <div className='container'>
                    <center>
                        <video id={Sup.vdo} controls >
                            <source src="https://youtu.be/ckXKVfSdlDw" />
                        </video>
                    </center>
                    </div>
                </div>
            </div>
        </div>
        <div className="container">
            <div className='text-center my-5'>
                <h1 id={Sup.txtH}>لماذا معامل المختبر لفحص الاغذية والمياه؟</h1>
                <p id={Sup.txt}>في معامل المختبر نفخر بأنفسنا  بجودة خدماتنا. يعمل فريق من علماء الميكروبيولوجي للأغذية 
                    <br />
                واطباء متخصصين يعملون معك لتقديم تقرير مخصص يناسب احتياجاتك. نحن نعرف <br /> ونقدر كل من عملائنا.</p>
            </div>
        </div>
        <div className="container">
            <div className='text-right'>
                <h1 className='text-right' id={Sup.txtH}>نجاحنا يأتي من</h1>
            </div>
            <br />
            <div className="row">
                <div className="col-md-6 text-right">
                    <ul id={Sup.ULLista}>
                        <li id={Sup.LILista}><p> اسعار مناسبة للشركات</p></li>
                        <li id={Sup.LILista}><p>تقارير مفصلة</p></li>
                        <li id={Sup.LILista}><p>تغطية كاملة لجميع انحاء الجمهورية</p></li>
                        <li id={Sup.LILista}><p>شهادات الاعتمادات</p></li>
                    </ul>
                </div>
                <div className="col-md-6 text-right">
                    <ul id={Sup.ULLista}>
                        <li id={Sup.LILista}><p>معامل المختبرعلامة تجارية موثوقة</p></li>
                        <li id={Sup.LILista}><p>أعلى تكنولوجيا</p></li>
                        <li id={Sup.LILista}><p>فريق فني محترف</p></li>
                        <li id={Sup.LILista}><p>دقة</p></li>
                    </ul>
                </div>
            </div>
        </div>
      </>
    )
  }
}
