import React, { Component } from 'react'
import StyleLogin from "./Login.module.css"
export default class Login extends Component {
  render() {
    return (
      <>
      <div className="container my-5 text-right" id={StyleLogin.frstBox}>
        <form className='m-auto' id={StyleLogin.Forms}>
                <div className="form-group ">
                    <label htmlFor="exampleInputEmail1">اسم المستخدم</label>
                    <input type="email" className="form-control" id="exampleInputEmail1" aria-describedby="emailHelp" />
                    <small id="emailHelp" className="form-text text-muted">لن نشارك بريدك الإلكتروني مع أي شخص آخر</small>
                </div>
                <div className="form-group ">
                    <label htmlFor="exampleInputPassword1">كلمه المرور</label>
                    <input type="password" className="form-control" id="exampleInputPassword1" />
                </div>
                <div className="form-group form-check">
                    <input type="checkbox" className="form-check-input" id="exampleCheck1" />
                    <label className="form-check-label" htmlFor="exampleCheck1">تذكرنى</label>
                </div>
                <a href="/">هل نسيت كلمة المرور </a>
                <br />
                <br />
                <button type="submit" className="btn btn-danger ml-5">تسجيل الدخول</button>
                <br />
                <br />
                <a href="/signup" className='text-dark font-weight-bold'>إنشاء حساب جديد</a>
            </form>
      </div>

      </>
    )
  }
}
