import React, { Component } from 'react'
import StyleDetail from "./Servicedetails.module.css"
import imgDetail from "../../imgs/smsImage.png"

export default class Servicedetails extends Component {
  render() {
    return (
      <>
        <div className="card text-white mb-5" id={StyleDetail.Card}>
            <img src={imgDetail} className="card-img" id={StyleDetail.CardImgee} alt="..." />
            <div className="card-img-overlay">
                <h1 className="card-title" id={StyleDetail.hdOne}>توصيل النتائج</h1>
                <center>
                    <button id={StyleDetail.btn} className='btn btn-transparent'>احجز الآن</button>
                </center>
            </div>
        </div>
        <br />
        <div className="container mt-2 mb-5">
            <div className="row">
                <div className="col-md-12 text-right">
                    <h1>وصف الخدمة</h1>
                    <p id={StyleDetail.txtPgf}>إذا كنت لا تريد العودة إلى فروعنا لاستلام نتائجتك، فقط قم بإبلاغ موظف الاستقبال وسوف نقوم بتسليمها إلى عنوان من اختيارك مقابل رسم رمزي. بدلاً من ذلك كما يمكنك عرض النتائج الخاصة بك على الانترنت مجاناً في أي وقت وفي أي مكان</p>
                </div>
            </div>
        </div>
      </>
    )
  }
}
