import React, { Component } from 'react'
import Ftr from "./Footer.module.css"
import FaceBook from "../../imgs/facebook.png"
import Instagram from "../../imgs/instagram.png"
import Youtube from "../../imgs/youtube.png"
import logoFooter from "../../imgs/logo.png"
export default class Footer extends Component {
  render() {
    return (
      <>
      <div className="container">
          <div className="row" id={Ftr.BoxOne}>
              <div className="col-md-6 text-left my-2" >
                  <ul id={Ftr.UList}>
                      <li id={Ftr.LList}><img id={Ftr.iconSocialF} src={FaceBook} alt="" /></li>
                      <li id={Ftr.LList}><img id={Ftr.iconSocial} src={Instagram} alt="" /></li>
                      <li id={Ftr.LList}><img id={Ftr.iconSocial} src={Youtube} alt="" /></li>
                  </ul>
              </div>
              <div className="col-md-6 text-right my-2">
                  <ul id={Ftr.UList}>
                      <li id={Ftr.LList}><a id={Ftr.txtLink} href="/callus">تواصل معنا</a></li>
                      <li id={Ftr.LList}><a id={Ftr.txtLink} href="/branches">فروعنا</a></li>
                      <li id={Ftr.LList}><a id={Ftr.txtLink} href="/home">برامج الولاء</a></li>
                      <li id={Ftr.LList}><a id={Ftr.txtLink} href="/services">خدمات الافراد</a></li>
                      <li id={Ftr.LList}><a id={Ftr.txtLink} href="/services">خدمات الشركات</a></li>
                      <li id={Ftr.LList}><a id={Ftr.txtLink} href="/packages">العروض</a></li>
                  </ul>
              </div>
              <div className="col-md-6 text-left my-2">
                  <ul id={Ftr.UList}>
                      <li id={Ftr.LList}><a href="/home"><img id={Ftr.iconSocialF} src={logoFooter} className='w-50' alt="" /></a></li>
                  </ul>
              </div>
              <div className="col-md-6 text-right my-2">
                  <ul id={Ftr.UList}>
                      <li id={Ftr.LList}><p>جميع الحقوق محفوظة Ⓒ لمجموعة معامل المختبر 2021</p></li>
                  </ul>
              </div>
          </div>
      </div>
      </>
    )
  }
}
