import React, { Component } from 'react'
import S from "./Services.module.css"
import ELECT from "../../imgs/download (1).png"
import SMS from "../../imgs/download.png"
import House from "../../imgs/375x200 House Visit copy.png"

export default class Services extends Component {
  render() {
    return (
      <>
      <div className="container" id={S.BoxOne}>
        <div className='text-right'>
            <h1 id={S.txtServises}>خدماتنا</h1>
        </div>
      </div>

        <div className="card-deck" id={S.CardDeck}>
        <div className="card" id={S.cardBox}>
            <img src={ELECT} id={S.CardImg} className="card-img-top" alt="..." />
            <div className="card-body">
              <h5 className={S.cardtitle}>الخدمات الالكترونيه</h5>
              <p className={S.cardtext}>الخدمات الالكترونيه</p>
            </div>
        </div>
        <div className="card" id={S.cardBox}>
            <img src={SMS} id={S.CardImg} className="card-img-top" alt="..." />
            <div className="card-body"> 
            <a href="/servicedetails" className='text-dark text-decoration-none'>
              <h5 className={S.cardtitle}>توصيل النتائج</h5>
              <p className={S.cardtext}>توصيل النتائج</p>
            </a>
            </div>
        </div>
        <div className="card" id={S.cardBox}>
            <img src={House} id={S.CardImg} className="card-img-top" alt="..." />
            <div className="card-body">
            <h5 className={S.cardtitle}>الزيارات المنزلية</h5>
            <p className={S.cardtext}>الزيارات المنزلية</p>
            </div>
        </div>
        </div>
      </>
    )
  }
}
