import React, { Component } from 'react'
import StyleSign from "./Signup.module.css"

export default class Signup extends Component {
  render() {
    return (
      <>
        <div className="container my-5 text-right" id={StyleSign.frstBox}>
            <form className='m-auto' id={StyleSign.Forms}>
                    <div className="form-group ">
                        <label htmlFor="exampleInputFirstName">الاسم الاول</label>
                        <input type="text" className="form-control" id="exampleInputFirstName" aria-describedby="nameHelp" />
                        <small id="nameHelp" className="form-text text-muted">لن نشارك بريدك الإلكتروني مع أي شخص آخر</small>
                    </div>
                    <div className="form-group ">
                        <label htmlFor="exampleInputSecondName">الاسم الثاني</label>
                        <input type="text" className="form-control" id="exampleInputSecondName" aria-describedby="nameHelp" />
                        <small id="nameHelp" className="form-text text-muted">لن نشارك بريدك الإلكتروني مع أي شخص آخر</small>
                    </div>
                    <div className="form-group ">
                        <label htmlFor="exampleInputEmail1">البريد الإلكتروني</label>
                        <input type="email" className="form-control" id="exampleInputEmail1" aria-describedby="emailHelp" />
                        <small id="emailHelp" className="form-text text-muted">لن نشارك بريدك الإلكتروني مع أي شخص آخر</small>
                    </div>
                    <div className="form-group ">
                        <label htmlFor="exampleInputphoneNumber1">رقم الجوال</label>
                        <input type="tel" className="form-control" id="exampleInputphoneNumber1" aria-describedby=" telHelp" />
                        <small id=" telHelp" className="form-text text-muted">لن نشارك بريدك الإلكتروني مع أي شخص آخر</small>
                    </div>
                    <div className="form-group ">
                        <label htmlFor="exampleInputPassword1">كلمه المرور</label>
                        <input type="password" className="form-control" id="exampleInputPassword1" />
                    </div>
                    <div className="form-group">
                        <label htmlFor="exampleInputPassword2">تاكيد كلمه المرور</label>
                        <input type="password" className="form-control" id="exampleInputPassword2" />
                    </div>
                    <div className="form-group form-check">
                        <input type="checkbox" className="form-check-input" id="exampleCheck1" />
                        <label className="form-check-label" htmlFor="exampleCheck1">تذكرنى</label>
                    </div>
                    <button type="submit" className="btn btn-danger ml-5">تسجيل الدخول</button>
                    <br />
                    <br />
                    <span>او</span>
                    <br />
                    <br />
                    <button type="submit" className="btn btn-dark ml-5"> تسجيل بواسطة الفيسبوك</button>
                </form>
      </div>
      </>
    )
  }
}
