import React, { Component } from 'react'
import CalStyle from "./Callus.module.css"
export default class Callus extends Component {
  render() {
    return (
      <>
      <section id={CalStyle.fulBg}>
          <div className="container">
          <h1 className='text-right'>اتصل بنا</h1>
          </div>
        <div className="container mt-5 mb-5">
            <div className="row">
                <div className="col-sm-4">
                    <div className="card text-white text-right" id={CalStyle.CardBorder}>
                    <div className="card-body">
                        <h6 id={CalStyle.hdr} className="card-title">فيس بوك</h6>
                        <a href="/Facebook.com/almokhtabar" className='text-white'>Facebook.com/almokhtabar</a>
                        <br />
                        <br />
                        <h6  id={CalStyle.hdr}  className="card-title">انستجرام</h6>
                        <a href="/Instagram.com/almokhtabar" className='text-white'>Instagram.com/almokhtabar</a>
                        <br />
                        <br />
                        <h6  id={CalStyle.hdr}  className="card-title">البريد الالكتروني</h6>
                        <a href="/info@almokhtabar.com" className='text-white'>info@almokhtabar.com</a>
                        <br />
                        <br />
                        <h6 id={CalStyle.hdr} className="card-title">الخط الساخن</h6>
                        <a href="/to" className='text-white'>19014</a>
                    </div>
                    </div>
                </div>
                <div className="col-sm-8 text-right">
                    <div className="card" id={CalStyle.CardBorderTwo}>
                    <div className="card-body">
                        <h3>الإقتراحات</h3>
                        <form className='text-right'>
                            <div className="form-row">
                                <div className="form-group col-md-6">
                                <label htmlFor="inputEmail4">البريد الإلكتروني</label>
                                <input type="email" className="form-control" id="inputEmail4" />
                                </div>
                                <div className="form-group col-md-6">
                                <label htmlFor="inputname4">الاسم</label>
                                <input type="text" className="form-control" id="inputname4" />
                                </div>
                            </div>
                            <div className="form-group">
                                <label htmlFor="inputnum">رقم الجوال</label>
                                <input type="tel" className="form-control w-100 text-right" id="inputnum" />
                            </div>
                            <div className="form-group text-right">
                                <label htmlFor="inputSubject">الموضوع</label>
                                <input type="text" className="form-control w-100 text-right" id="inputSubject" />
                            </div>
                            <div className="form-group text-right">
                            <label htmlFor="validationTextarea">الرساله</label>
                            <textarea className="form-control text-right" id="validationTextarea" required></textarea>
                            </div>
                            <button type="submit" className="btn btn-dark">ارسال</button>
                            <button type="submit" className="btn btn-danger ml-5">الغاء</button>
                        </form>
                    </div>
                    </div>
                </div>
            </div>
        </div>
      </section>

      </>
    )
  }
}
