import React, { Component } from 'react'
import Scr from "./ScrollToTop.module.css"
export default class ScrollToTop extends Component {
  render() {
      onclick = function (){
          window.scrollTo({
              top: 0,
              behavior: 'smooth'
            })
        }
    return (
      <>
      <button className='btn btn-danger' id={Scr.up}>Up</button>
      </>
    )
  }
}
