import React from 'react'
import Logo from "../../imgs/logo.png"
import Search from "../../imgs/search1.png"
import NB from "./Navbar.module.css"

export default function Navbar() {
  return (
    <>
    <div className="container">
      <div className="row text-center my-2">
        <div className="col-md-4 text-left">
          <a href="/to" id={NB.ConTxt}>ENG </a>
          <a href="/to" id={NB.ConTxt}> 19014</a>
        </div>
        <div className="col-md-4">
          <a href="/home">
            <img src={Logo} className='w-50' alt="" />
          </a>
        </div>
        <div className="col-md-4 text-right">
          <a href="/to" id={NB.ConTxt}> تسجيل الدخول</a>
        </div>
      </div>
    </div>

    <nav className="navbar sticky-top navbar-expand-lg" id={NB.NAV}>
      <button className="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
        <span className="navbar-toggler-icon" id={NB.icon}></span>
      </button>
    <div className="collapse navbar-collapse" id="navbarSupportedContent">
      <form className="form-inline my-2 my-lg-0 m-auto">
          <a className="navbar-brand text-white" id={NB.BTN} href="/to">نتائج التحاليل</a>
          <button className="btn  my-2 my-sm-0" type="submit"><img src={Search} alt="" /></button>
        </form>
        <ul className="navbar-nav m-auto">
          <li className="nav-item active">
            <a className="nav-link text-white" id={NB.AnckorLink} href="/support">دعم المؤسسات</a>
          </li>
          <li className="nav-item">
            <a className="nav-link text-white" id={NB.AnckorLink} href="/preparation">التحضيرات</a>
          </li>
          <li className="nav-item">
            <a className="nav-link text-white" id={NB.AnckorLink} href="/packages">الباقات</a>
          </li>
          <li className="nav-item">
            <a className="nav-link text-white" id={NB.AnckorLink} href="/login">الحجز</a>
          </li>
          <li className="nav-item">
            <a className="nav-link text-white" id={NB.AnckorLink} href="/lab">المختبر</a>
          </li>
          <li className="nav-item">
            <a className="nav-link text-white" id={NB.AnckorLink} href="/branches">فروعنا</a>
          </li>
          <li className="nav-item">
            <a className="nav-link text-white" id={NB.AnckorLink} href="/services">الخدمات</a>
          </li>
          <li className="nav-item">
            <a className="nav-link text-white" id={NB.AnckorLink} href="/about">عن معامل المختبر</a>
          </li>
        </ul>
      </div>
    </nav>
    </>
  )
}
