import React, { Component } from 'react'
import L from "./Lab.module.css"
import imgTwo from "../../imgs/almokmore (1).png"
import imgThree from "../../imgs/MkPartners.png"
import imgFour from "../../imgs/MK.png"
import Android from "../../imgs/android.png"
import Ios from "../../imgs/ios.png"
export default class Lab extends Component {
  render() {
    return (
      <>
      <div className="container" id={L.BoxOne}>
          <div className="row">
              <div className="col-md-12">
                  <img src={imgTwo} id={L.image} alt="" />
              </div>
          </div>
          <div className="row">
              <div className="col-md-12 text-right">
                  <p id={L.txtCont}>يتيح لك برنامج ALMOKHATABR MORE، المقدم من معامل المختبر، التنقل بين ثلاثة فئات للحصول على مكافآت حصرية يمكنك التحكم فيها بشكل كامل والوصول إليها بسهولة. اجمع النقاط مع كل زيارة لفروع المختبر أو الزيارات المنزلية، ثم استبدلها كطريقة دفع في زياراتك اللاحقة من خلال عملية بسيطة وسريعة. يمكنك أيضًا كسب نقاط إضافية في المناسبات الخاصة، والاستمتاع بالعروض الحصرية لدى العديد من شركائنا , جمع القسائم وأكثر!</p>
              </div>
          </div>

          <br />

          <div className="row">
              <div className="col-md-12 text-right">
                  <div>
                    <span>لمعرفه العروض و الحصول عليها <a href="/">اضغط هنا</a>  او حمل التطبيق</span>
                    <p>لتسجيل الدخول برجاء استخدام اسم المستخدم وكلمه السر المرفقه بإيصال اخر زياره او اتصل ب 
                        <a href="/">19014</a>
                    </p>
                  </div>
              </div>
          </div>
          <div className="row">
              <div className="col-md-12">
                  <div className="card" id={L.CARDSEC}>
                  <img src={imgThree} alt="" />
                  </div>
              </div>
          </div>
      </div>

        <div className="text-right" id={L.head}>
            <h1> كيف يعمل البرنامج ؟</h1>
        </div>

        <div className="card-deck" id={L.CardDeck}>
            <div className="card" id={L.Card}>
                <div className="card-body text-center">
                    <svg id={L.SVG} xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24"><g fill='#DF1C2D'><path d="M12 21c1.7 0 3-1.3 3-3H9c0 1.7 1.3 3 3 3zm7-6.6c-3.2-2.6 1-7.1-5-9.4 0-3-4-3-4 0-6 2.4-1.8 6.9-5 9.4-1 1-.3 2.6 1 2.6h12c1.3 0 2-1.6 1-2.6z"></path></g></svg>
                    <p className="card-text" id={L.pargrph}>احصل على تنبيه عند كل عملية تجميع واستبدال للنقاط</p>
                </div>
            </div>
            <div className="card" id={L.Card}>
                <div className="card-body text-center">
                    <svg id={L.SVG} xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24"><g fill='#DF1C2D'><path d="M20 8h-3V6c0-1.1-.9-2-2-2H9c-1.1 0-2 .9-2 2v2H4c-1.1 0-2 .9-2 2v8c0 1.1.9 2 2 2h16c1.1 0 2-.9 2-2v-8c0-1.1-.9-2-2-2zM9 6.5c0-.3.2-.5.5-.5h5c.3 0 .5.2.5.5V8H9V6.5z"></path></g></svg>
                    <p className="card-text" id={L.pargrph}>استبدل هذه النقاط في أي من فروعنا بأي خدمة تختارها</p>
                </div>
            </div>
            <div className="card" id={L.Card}>
                <div className="card-body text-center">
                    <svg id={L.SVG} xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24"><g fill='#DF1C2D'><circle cx="12" cy="12" r="3"></circle><path d="M12 3c-5 0-9 4-9 9s4 9 9 9 9-4 9-9-4-9-9-9zm0 15c-3.3 0-6-2.7-6-6s2.7-6 6-6 6 2.7 6 6-2.7 6-6 6z"></path></g></svg>
                    <p className="card-text" id={L.pargrph}>اجمع النقاط عند كل عملية شراء ناجحة لخدمة من خدماتنا</p>
                </div>
            </div>
        </div>

        <div className="container">
          <div className="row">
              <div className="col-md-12 text-right">
                  <div>
                      <span  id={L.txtCont}>لمعرفه نقاطك <a href="/">اضغط هنا</a>  او حمل التطبيق من </span>
                    <p  id={L.txtCont}>لتسجيل الدخول برجاء استخدام اسم المستخدم وكلمه السر المرفقه بإيصال اخر زياره او اتصل ب <a href="/">19014</a></p>
                  </div>
              </div>
          </div>
      </div>
      <br />
      <div className="container">
          <div className="row">
              <div className="col-md-6 text-right" id={L.cardLeftMob}>
                  <div className='text-right'>
                      <h1 id={L.txtMob}>حمل تطبيق معامل المختبر</h1>
                      <p id={L.paghMob}>حمل تطبيق معامل المختبر للحجز و الإطلاع على نتائج التحاليل و معرفة كل التحضيرات اللازمة لكل أنواع التحاليل</p>
                      <ul id={L.Ul}>
                          <li id={L.Li}><img src={Android} className='w-100' alt="" /></li>
                          <li id={L.Li}><img src={Ios} className='w-100' alt="" /></li>
                      </ul>
                  </div>
              </div>
              <div className="col-md-6 text-right">
            <img src={imgFour} id={L.imageMob} alt="" />
              </div>
          </div>
      </div>
      <div className="container">
          <div className="row" id={L.LeastBox}>
              <div className="col-md-12 text-right">
                  <h1>الشروط والاحكام الأساسية</h1>
              </div>
          </div>
      </div>
      <div className="container">
          <div className="row" id={L.LastBox}>
              <div className="col-md-12 text-right">
                  <ul id={L.UList}>
                      <li id={L.LList}><p>يعتبر اشتراك العميل في برنامج نقاط ومزايا الولاء المتاح من قبل الشركة وكذلك استبداله لأي من تلك النقاط في أي وقت من الاوقات، بمثابة اقرار وموافقة من العميل على الشروط والاحكام الخاصة ببرنامج نقاط ومزايا الولاء</p></li>
                      <li id={L.LList}><p> قد يتم تحديث الشروط والاحكام من أن لأخر حسبما يتراءى للشركة، وعلى أن تتاح الشروط والاحكام وكذلك أي من تحديثاتها من خلال الموقع الالكتروني أو التطبيقات الالكترونية للشركة، وتسري ويعمل بها فور نشرها</p></li>
                      <li id={L.LList}><p>يتم تجميع النقاط بناءاً على القيم المادية للتحاليل والفحوصات المطلوبة والمسددة من قبل العميل</p></li>
                      <li id={L.LList}><p>يمكن للعميل استبدال النقاط المتاحة لديه بدءاً من الزيارة التي تلي الزيارة التي استحقت وسجلت النقاط لدى العميل بسببها</p></li>
                      <li id={L.LList}><p>يتم استخدام النقاط المتاحة لدى العميل في صورة خصومات على الفحوصات والتحاليل التي يطلبها العميل في الزيارات التالية، ولا يتم استبدال النقاط بصورة نقدية</p></li>
                      <li id={L.LList}><p> تعتبر النقاط صالحة للاستخدام من قبل العميل في خلال مدة اقصاها 24 شهرا من تاريخ استحقاقها وتسجليها على حساب العميل</p></li>
                      <li id={L.LList}><p>قد تتيح الشركة في أي من الاوقات، سواء بصورة طويلة الاجل أو من خلال عروض ترويجية مؤقتة، تجميع نقاط لعدد من العملاء، على ان يكون هذا التجميع فقط من خلال برامج تجميع نقاط الولاء المصممة للعائلات والمقربين</p></li>
                  </ul>
              </div>
          </div>
      </div>


      </>
    )
  }
}
