import React, { Component } from 'react'
import ChatStyle from "./Chat.module.css"
import ChatBoot from "../../imgs/chatOne.png"

export default class Chat extends Component {
  render() {
    return (
      <>
      <div className="alert alert-dismissible fade show text-right" id={ChatStyle.Alert} role="alert">
        <strong>أنا مساعدك الذكي وموجود <br /> عشان أرد على استفساراتك</strong>
        <button type="button" className="close" data-dismiss="alert" aria-label="Close">
            <span aria-hidden="true" className='text-danger font-weight-bolder'>&times;</span>
        </button>
    </div>
      <div className='text-left' id={ChatStyle.divChat}>
          <div className='text-center'>
          <img src={ChatBoot} className='w-100' alt="" />
          </div>
      </div>
      </>
    )
  }
}
