import React, { Component } from 'react'
import B from "./Branches.module.css"
export default class Branches extends Component {
  render() {
    return (
      <>
      <div className="container-fluid" id={B.BoxOne}>
            <div className="row">
              <div className="col-md-6">
                <div className={B.SelectManualy}>
                    <button className='btn btn-info' id={B.btnResult}>تصفية النتائج</button>
                </div>
              </div>
              <div className="col-md-6 text-right">
                <div className={B.SelectBranches}>
                    <h1 id={B.txtAbout}>فروعنا</h1>
                </div>
              </div>
            </div>
        </div>

        <div className="card-deck" id={B.CardDeck}>
            <div className="card" id={B.BoxChild}>
                <div className="card-body">
                    <button className='btn btn-info' id={B.btnRemove}>الغاء</button>
                </div>
            </div>
            <div className="card" id={B.BoxChild}>
                <div className="card-body">
                    <select id={B.contxt} className="form-control form-control-sm">
                        <option>الفرع</option>
                    </select>
                </div>
            </div>
            <div className="card" id={B.BoxChild}>
                <div className="card-body">
                    <select id={B.contxt} className="form-control form-control-sm">
                        <option>المحافظة</option>
                    </select>
                </div>
            </div>
        </div>

        <div className="container-fluid" id={B.boxBranches}>
            <div className='text-right' id={B.heading}>
                <h1 id={B.heading}>القاهرة</h1>
            </div>
            <div className="row" id={B.BarnchesCity}>
                <div className="col-md-6">
                    <button className='btn btn-danger' id={B.BtnWeb}> الموقع</button>
                </div>
                <div className="col-md-6 text-right">
                    <h4>المعادى 4</h4>
                    <p id={B.txtP}>مرتفع القطامية ابراج سما القاهره برج (و9) بجوار برج سما الادارى ومستشفى البنك الاهلى - الشقه رقم (1-2) الدور الاول</p>
                </div>
            </div>
            <div className="row" id={B.BarnchesCity}>
                <div className="col-md-6">
                    <button className='btn btn-danger' id={B.BtnWeb}> الموقع</button>
                </div>
                <div className="col-md-6 text-right">
                    <h4>معادى 5 (زهراء)</h4>
                    <p id={B.txtP}>1 عمارات طيبه (د)- زهراء المعادى الدور الاول علوى شقة 104</p>
                </div>
            </div>
            <div className="row" id={B.BarnchesCity}>
                <div className="col-md-6">
                    <button className='btn btn-danger' id={B.BtnWeb}> الموقع</button>
                </div>
                <div className="col-md-6 text-right">
                    <h4>عزبه النخل</h4>
                    <p id={B.txtP}>107 شارع الصحة - شارع السكه الحديد سابقا - عزبه النخل الشرقيه</p>
                </div>
            </div>
            <div className="row" id={B.BarnchesCity}>
                <div className="col-md-6">
                    <button className='btn btn-danger' id={B.BtnWeb}> الموقع</button>
                </div>
                <div className="col-md-6 text-right">
                    <h4>صلاح سالم</h4>
                    <p id={B.txtP}>عمارة 9 عمارات العبور - شارع صلاح سالم</p>
                </div>
            </div>
            
            <div className="row" id={B.BarnchesCity}>
                <div className="col-md-6">
                    <button className='btn btn-danger' id={B.BtnWeb}> الموقع</button>
                </div>
                <div className="col-md-6 text-right">
                    <h4>El Batreek</h4>
                    <p id={B.txtP}>4 شارع محمد عباس متفرع من شارع العشرين عين شمس الشرقية</p>
                </div>
            </div>
        </div>
      </>
    )
  }
}
