import React, { Component } from 'react'
import HomeStyle from "./Home.module.css"
import SlideOne from "../../imgs/imgSlider1.jpg"
import SlideTwo from "../../imgs/imgSlider2.jpg"
import SlideThree from "../../imgs/imgSlider3.jpg"
import photosOne from "../../imgs/download.png"
import photosTwo from "../../imgs/375x200 House Visit copy.png"
import photosThree from "../../imgs/download (1).png"
import VistImg from "../../imgs/VisitImg.jpg"
import androidMobile from "../../imgs/android.png"
import iosMobile from "../../imgs/ios.png"
import MobilePhone from "../../imgs/MK.png"
import FceBok from "../../imgs/facebook.png"
import Instgrm from "../../imgs/instagram.png"
import Yotub from "../../imgs/youtube.png"
export default class Home extends Component {
  render() {
    return (
      <>
      <div id="carouselExampleControls" className="carousel slide" data-ride="carousel">
            <div className="carousel-inner">
                <div className="carousel-item active">
                <img src={SlideOne} className="d-block w-100" alt="..." />
                </div>
                <div className="carousel-item">
                <img src={SlideTwo} className="d-block w-100" alt="..." />
                </div>
                <div className="carousel-item">
                <img src={SlideThree} className="d-block w-100" alt="..." />
                </div>
            </div>
            <button className="carousel-control-prev" type="button" data-target="#carouselExampleControls" data-slide="prev">
                <span className="carousel-control-prev-icon" aria-hidden="true"></span>
                <span className="sr-only">Previous</span>
            </button>
            <button className="carousel-control-next" type="button" data-target="#carouselExampleControls" data-slide="next">
                <span className="carousel-control-next-icon" aria-hidden="true"></span>
                <span className="sr-only">Next</span>
            </button>
       </div>

       <div className="container">
        <div className='text-right' id={HomeStyle.HedHOne}>
            <h1>خدماتنا</h1>
        </div>
        <div className='text-left' id={HomeStyle.Semore}>
            <a href="/to" id={HomeStyle.AnkorLink}>المزيد</a>
        </div>
      </div>

        <div className="card-deck" id={HomeStyle.CardDeck}>
        <div className="card" id={HomeStyle.Card}>
            <img src={photosThree} id={HomeStyle.CardImg}  className="card-img-top" alt="..." />
            <div className="card-body">
            <h5 id={HomeStyle.cardtitle}>الخدمات الالكترونيه</h5>
            <p id={HomeStyle.cardtext}>الخدمات الالكترونيه</p>
            </div>
        </div>
        <div className="card" id={HomeStyle.Card}>
            <img src={photosOne} id={HomeStyle.CardImg}  className="card-img-top" alt="..." />
            <div className="card-body"> 
            <h5 id={HomeStyle.cardtitle}>توصيل النتائج</h5>
            <p id={HomeStyle.cardtext}>توصيل النتائج</p>
            </div>
        </div>
        <div className="card" id={HomeStyle.Card}>
            <img src={photosTwo} id={HomeStyle.CardImg}  className="card-img-top" alt="..." />
            <div className="card-body">
            <h5 id={HomeStyle.cardtitle}>الزيارات المنزلية</h5>
            <p id={HomeStyle.cardtext}>الزيارات المنزلية</p>
            </div>
        </div>
        </div>

        <br />

        <div className="container">
        <div className="row">
            <div className="col-sm-6">
                <div className="card text-left" id={HomeStyle.BoxOne}>
                    <div className="card-body">
                        <img src={VistImg} className='w-100' alt="" />
                    </div>
                </div>
            </div>
            <div className="col-sm-6">
                <div className="card text-right" id={HomeStyle.BoxOne}>
                <div className="card-body">
                    <h5 className="card-title" id={HomeStyle.titleOne}>خدمة الزيارات المنزلية</h5>
                    <p className="card-text" id={HomeStyle.titleTwo}>تقدر تعمل التحاليل و انت فى مكانك فقط احجز زيارة منزلية و هنجيلك لأى مكان</p>
                    <a href="/to" className="btn btn-primary my-5"> احجز الان </a>
                </div>
                </div>
            </div>
        </div>
        </div>

        <br />

        <div className="container-fluid" id={HomeStyle.BackG}>
        <div className="row">
            <div className="col-sm-6">
                <div className="card text-right" id={HomeStyle.lastSection}>
                    <div className="card-body">
                        <h5 className="card-title text-right" id={HomeStyle.titleThre}>حمل تطبيق معامل المختبر</h5>
                        <p className="card-text" id={HomeStyle.titleFour}>حمل تطبيق معامل المختبر للحجز و الإطلاع على نتائج التحاليل و معرفة كل التحضيرات اللازمة لكل أنواع التحاليل</p>
                        <ul id={HomeStyle.ULLISTING}>
                            <li id={HomeStyle.LLLISTING}><img src={androidMobile} className='w-100' alt="" /></li>
                            <li id={HomeStyle.LLLISTING}><img src={iosMobile} className='w-100' alt="" /></li>
                        </ul>
                    </div>
                </div>
            </div>
            <div className="col-sm-6">
                <div className="card text-right" id={HomeStyle.lastSection}>
                    <div className="card-body">
                        <img src={MobilePhone} id={HomeStyle.Mobileimages} alt="" />
                    </div>
                </div>
            </div>
        </div>
        </div>

        <div className='text-left' id={HomeStyle.sideBar}>
          <div id={HomeStyle.slidbrOne}>
            <ul id={HomeStyle.sidBrUl}>
                <li id={HomeStyle.sidBrLL}><img src={FceBok} id={HomeStyle.mtImg} alt="" /></li>
            </ul>
          </div>
          <div id={HomeStyle.slidbrTwo}>
            <ul id={HomeStyle.sidBrUl}>
                <li id={HomeStyle.sidBrLL}><img src={Instgrm} id={HomeStyle.mtImgins} alt="" /></li>
            </ul>
          </div>
          <div id={HomeStyle.slidbrThre}>
            <ul id={HomeStyle.sidBrUl}>
                <li id={HomeStyle.sidBrLL}><img src={Yotub} id={HomeStyle.mtImgY} alt="" /></li>
            </ul>
          </div>
      </div>
      </>
    )
  }
}
