import React, { Component } from 'react'
import StylePackg from "./Packagedetails.module.css"
import PaskImgDetail from "../../imgs/Package.jpg"
export default class Packagedetails extends Component {
  render() {
    return (
      <>
              <div className="card text-white mb-5" id={StylePackg.Card}>
            <img src={PaskImgDetail} className="card-img" id={StylePackg.CardImgee} alt="..." />
            <div className="card-img-overlay">
                <h1 className="card-title" id={StylePackg.hdOne}>توصيل النتائج</h1>
                <center>
                    <button id={StylePackg.btn} className='btn btn-transparent'>احجز الآن</button>
                </center>
            </div>
        </div>
        <br />
        <div className="container mt-2 mb-5">
            <div className="row">
                <div className="col-md-12 text-right">
                    <h1>تفاصيل العرض</h1>
                    <br />
                    <span>الإختبارات</span>
                    <ul id={StylePackg.UseUl}>
                      <li><p id={StylePackg.txtPgf}>فحص التهاب الكبد سي</p></li>
                      <li><p id={StylePackg.txtPgf}>تحليل انزيمات الكبد SGPT</p></li>
                      <li><p id={StylePackg.txtPgf}>تتحليل انزيمات الكبد SGOT</p></li>
                      <li><p id={StylePackg.txtPgf}>فحص التهاب الكبد بي</p></li>
                    </ul>
                </div>
            </div>
        </div>
      </>
    )
  }
}
